import Vue from 'vue'
import Router from 'vue-router'

import uiHello from '@/components/Hello'
import inspectionManager from '@/components/table/inspectionManager'
import runingLog from '@/components/table/runingLog'
import attendanceInfo from '@/components/table/attendanceInfo'
import checkPerson from '@/components/table/checkPerson'

Vue.use(Router)

const template = {
  template: "<router-view></router-view>"
}

export default new Router({
  routes: [
    {
      path: '/',
      component: uiHello
    },
    {
      path: '/uiHello',
      name: 'uiHello',
      component: uiHello
    }, 
    {
      path: '/table',
      name: 'table',
      component: template,
      children: [
        {
        path: 'attendanceInfo',
        component: attendanceInfo
        },
        {
        path: 'inspectionManager',
        component: inspectionManager
        },
        {
        path: 'runingLog',
        component: runingLog
        },
		{
        path: 'checkPerson',
        component: checkPerson
        }
    ]
    }
  ]
})



