
const axios = require('axios');
axios.defaults.baseURL = 'http://r3x0867731.imdo.co';
const url = 'http://r3x0867731.imdo.co'
export function attendList(data,callback) {
    axios.get('/attend/list',data,{headers:{'Access-Control-Allow-Origin': '*'}}).then((res)=>{
      // return res.data.data.data;
      // callback({ success: true, results: res.data.data.data })
      res.json({ success: true, results: res.data.data });
    }).catch((err)=>{

    });
}
export function attendAdd(data) {
    axios.post(url + '/attend/add',data);
}


