<template>
    <a @click="toggle" :class="{active:isfull}"><i class="fa fa-expand fa-fw text"></i> <i class="fa fa-compress fa-fw text-active"></i></a>
</template>
<script>
export default {
  data() {
    return {
      isfull: false
    }
  },
  methods: {
    fullscreenElement() {
      var fullscreenEle = document.fullscreenElement ||
        document.mozFullScreenElement ||
        document.webkitFullscreenElement;
      // fullscreenElenull
      // console.log("" + fullscreenEle);
      return fullscreenEle;
    },
    toggle() {
      if (this.fullscreenElement()) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.mozCancelFullScreen) { //moz
          document.mozCancelFullScreen();
        } else if (document.webkitExitFullscreen) { //webkit
          document.webkitExitFullscreen();
        }
        this.isfull=false;
      } else {
        var element = document.documentElement;
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.webkitRequestFullscreen) {
          element.webkitRequestFullscreen();
        } else if (element.msRequestFullscreen) {
          element.msRequestFullscreen();
        }
         this.isfull=true;
      }
    }
  }
}

</script>



