<template>
  <div class="attendanceInfo">
    <v-topTab selectPageTitle=""></v-topTab>
    <div class="wrapper-md" style="background-color: #ccccccbf;">
      <div class="panel panel-default">
        <div class="row wrapper">
          <label class="fl" style="line-height:3rem"></label>
          <div class="fl m-b-xs" style="line-height:2.8rem">
            <input type="date" v-model="queryData.params.startTime" /> -
            <input type="date" v-model="queryData.params.endTime" />
          </div>
          <button type="buttton" @click="searchList" class="btn btn_style fl"></button>
          <button type="buttton" @click="resetSearch" class="btn btn_style fl"></button>
			<a href="http://r3x0867731.imdo.co/attend/export" class="btn btn_style fr"></a>
        </div>
        <div class="table-responsive">
          <table class="table table-striped b-t b-light">
            <thead>
              <tr>
                
                <th>#</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody v-if="inspectionList.length>0">
              <tr v-for="(item,index) in inspectionList" :key="index">
                <td>{{(listData.page-1)*20+index+1}}</td>
                <td>{{item.username}}</td>
                <td>{{item.realname}}</td>
                <td>{{timeFormat(item.entryTime)}}</td>
                <td>{{timeFormat(item.outTime)}}</td>
                <td>{{item.duration}}</td>
                <td>
                  <a class="fl width50 opt" @click="updateByUserId(index)"></a>
                  <a class="fl width50 opt" @click="deleteByIdInfo(item.id)"></a>
                </td>
              </tr>
            </tbody>
          </table>
          <div v-if="inspectionList.length==0" class="cont"></div>
        </div>
      </div>
    </div>
    <footer
      class="panel-footer width"
      v-if="inspectionList.length>0"
      style="
    right: 0;
    bottom: 0;"
    >
      <div class="row">
        <div class="text-right text-center-xs fr" style="width: 80%;
    padding: 0px 3%;">
          {{(listData.page-1)*20+1}}-{{listData.page*20}}  {{listData.totalNum}}
          <span
            class="marginLeft1 opt"
            style="color:#2793e0"
            @click="page_click(1)"
          ></span>
          <span
            class="marginLeft1 opt"
            :style="listData.page==1?'color:#ccc':'color:#2793e0'"
            @click="page_click(listData.page-1,listData.page==1)"
          ></span>
          <span class="marginLeft1">{{listData.page}}</span>
          <span class="marginLeft1">{{listData.totalPage}}</span>
          <span
            class="marginLeft1 opt"
            :style="listData.page == listData.totalPage?'color:#ccc':'color:#2793e0'"
            @click="page_click(listData.page+1,listData.page == listData.totalPage)"
          ></span>
          <span
            class="marginLeft1 opt"
            style="color:#2793e0"
            @click="page_click(listData.totalPage)"
          ></span>
          <span class="marginLeft1"></span>
          <input
            type="number"
			onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )' 
            style="width:5rem;margin: 0 1%;"
            v-model="listData.page"
            min="1"
            :max="listData.totalPage"
          /> 
          <button @click="page_click(listData.page)"></button>
        </div>
      </div>
    </footer>
    <div id="test"></div>
    <div id="log_window">
      <div class="titleInfo width backgroundFFF">
        <h3 class="lineHeight5 cont" style="margin:0"></h3>
        <span class="fr closeX" @click="closeDlg">x</span>
      </div>
      <div class="titleInfo width marginTop1" style="height:100%;
    background-color: #fff;">
        <div class="item_info fl">
          <label class="width">{{obj.workNo}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.username}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.realname}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.entryTime}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.outTime}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.duration}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.inspectionTime}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.createBy}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.createTime}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.updateBy}}</label>
        </div>
        <div class="item_info fl">
          <label class="width">{{obj.updateTime}}</label>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import topTab from "../block/TopTab";
import { attendList, attendAdd } from "../../assets/api/axiosHttp";
export default {
  name: "attendanceInfo",
  data() {
    return {
      listData: {
        page: 1,
        totalNum: 0,
        totalPage: 1
      },
      inspectionList: [],
      checkAll: false,
      exportData: {
        startTime: "",
        endTime: "",
        idArrs: []
      },
      obj: {},
      queryData: {
        params: {
          endTime: "2020-04-14",
          page: 1,
          rows: 20,
          startTime: "2020-04-18"
        }
      }
    };
  },
  mounted() {
    this.queryData.params.startTime = this.getDate(10);
    this.queryData.params.endTime = this.getDate(-10);
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.$ajax
        .get("/attend/list", this.queryData, {
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          this.listData = response.data.data;
          this.inspectionList = response.data.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },
    getDate(n) {
      var n = n;
      var d = new Date();
      var year = d.getFullYear();
      var mon = d.getMonth() + 1;
      var day = d.getDate();
      if (day <= n) {
        if (mon > 1) {
          mon = mon - 1;
        } else {
          year = year - 1;
          mon = 12;
        }
      }
      d.setDate(d.getDate() - n);
      year = d.getFullYear();
      mon = d.getMonth() + 1;
      day = d.getDate();
      let s =
        year +
        "-" +
        (mon < 10 ? "0" + mon : mon) +
        "-" +
        (day < 10 ? "0" + day : day);
      return s;
    },
    page_click(page, flag) {
      if (flag == undefined) {
        this.queryData.params.page = page;
        this.init();
      } else {
        if (!flag) {
          this.queryData.params.page = page;
          this.init();
        }
      }
    },
    searchList() {
      this.init();
    },
    resetSearch() {
      this.queryData.params.startTime = this.getDate(10);
      this.queryData.params.endTime = this.getDate(-10);
      this.init();
    },
    addOpt() {
      console.info("=======addInfo=======");
      var s = document.getElementById("test");
      s.style.display = "block";

      var l = document.getElementById("log_window");
      l.style.display = "block";
    },
    exportOpt() {
      console.info("=======exportOpt=======");
      this.$ajax
        .get("/attend/export", this.exportData, {
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          if (response.data.code == 200) {
            alert("");
            this.init();
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    deleteByIdInfo(id) {
      console.info("=======deleteByIdInfo=======" + id);
      this.$ajax
        .delete("/attend/delete/" + id, {
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          if (response.data.code == 200) {
            alert("");
            this.init();
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    closeDlg() {
      var s = document.getElementById("test");
      s.style.display = "none";

      var l = document.getElementById("log_window");
      l.style.display = "none";
    },
    updateByUserId(index) {
      this.obj = this.inspectionList[index];
      this.obj.createTime = this.timeFormat(this.obj.createTime);
      this.obj.entryTime = this.timeFormat(this.obj.entryTime);
      this.obj.inspectionTime = this.timeFormat(this.obj.inspectionTime);
      this.obj.outTime = this.timeFormat(this.obj.outTime);
      this.obj.updateTime = this.timeFormat(this.obj.updateTime);
      console.info("=======updateByUserId=======" + index);
      var s = document.getElementById("test");
      s.style.display = "block";

      var l = document.getElementById("log_window");
      l.style.display = "block";
    },
    timeFormat(value) {
      let date = new Date(value);
      let Y = date.getFullYear() + "-";

      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";

      let D = date.getDate() + " ";

      let h = date.getHours() + ":";

      let m = date.getMinutes() + ":";

      let s = date.getSeconds();

      return Y + M + D + h + m + s;
    },
    setCheckAll(event) {
      let checked = event.target.checked;
      let arrs = document.getElementsByClassName("form-check-input");
      if (checked) {
        for (let i = 0; i < arrs.length; i++) {
          arrs[i].checked = true;
        }
        for (let a = 0; a < this.inspectionList.length; a++) {
          this.exportData.idArrs.push(this.inspectionList[a].id);
        }
      } else {
        for (let i = 0; i < arrs.length; i++) {
          arrs[i].checked = false;
        }
        this.exportData.idArrs = [];
      }
      console.info("exportData.idArrs" + this.exportData.idArrs);
    },
    setCheckOpt(event, id) {
      let checked = event.target.checked;
      let idArrs = this.exportData.idArrs;
      if (checked) {
        let flag = false;
        for (let a = 0; a < idArrs.length; a++) {
          if (idArrs[a] == id) {
            flag = true;
          }
        }
        if (!flag) {
          this.exportData.idArrs.push(id);
        }
      } else {
        for (let a = 0; a < idArrs.length; a++) {
          if (idArrs[a] == id) {
            this.exportData.idArrs.splice(a, 1);
          }
        }
      }
      console.info("exportData.idArrs" + this.exportData.idArrs);
    }
  },
  components: {
    "v-topTab": topTab
  }
};
</script>
<style scoped>
html,
body {
  margin: 0;
  height: 100%;
}
.attendanceInfo .item_info{
    margin: 2% 2%;
    width: 29%;
}
.attendanceInfo .lable_Style{
    width: 35%;
}
.attendanceInfo #test {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0.3;
  /*IE8*/
  filter: alpha(opacity=30);
  display: none;
}

.attendanceInfo #log_window {
  width: 60%;
  margin: 0 20%;
  height: 28%;
  background-color: #cccccc;
  position: absolute;
  margin-top: 5rem;
  z-index: 3;
  top: 20%;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
}
</style>


