<template>
  <div class="app-header navbar">
    <!-- navbar header -->
    <div class="navbar-header" :class="settings.navbarHeaderColor">
      <button class="pull-right visible-xs dk"> <i class="glyphicon glyphicon-cog"></i>
      </button>
      <button class="pull-right visible-xs"> <i class="glyphicon glyphicon-align-justify"></i>
      </button>
      <!-- brand -->
      <a href="#/" class="navbar-brand text-lt">
        <i class="fa fa-btc"></i>
        <span class="hidden-folded m-l-xs"></span>
      </a>
      <!-- / brand -->
    </div>
    <!-- / navbar header -->
    <!-- navbar collapse -->
    <div class="collapse pos-rlt navbar-collapse box-shadow" :class="settings.navbarCollapseColor">
      <!-- buttons -->
      <div class="nav navbar-nav hidden-xs">
        <a class="btn no-shadow navbar-btn" @click="toggleSidebar()">
          <i class="fa fa-dedent fa-fw" :class="{'fa-dedent':sidebar,'fa-indent':!sidebar}"></i>
        </a>
        <span></span>
      </div>
      <ul class="nav navbar-nav navbar-right">
        <li class="hidden-xs">
          <v-fullscreen></v-fullscreen>
        </li>
        <li class="dropdown" v-toggleClass="{class:'open',close:true}">
          <a class="dropdown-toggle">
            <i class="icon-bell fa-fw"></i>
            <span class="visible-xs-inline">Notifications</span>
            <span class="badge badge-sm up bg-danger pull-right-xs" id="Notifications">{{postTitle}}</span>
          </a>
          <!-- dropdown -->
          <div class="dropdown-menu w-xl animated fadeInUp">
            <div class="panel bg-white">
              <div class="panel-heading b-light bg-light"> <strong>You {{postTitle}} have
                  notifications</strong>
              </div>
              <div class="list-group">
                <a class="media list-group-item">
                  <span class="pull-left thumb-sm">
                    <img src="../../assets/app/img/a0.jpg" class="img-circle">
                  </span>
                  <span class="media-body block m-b-none">
                    <small class="text-muted">10 minutes ago</small>
                  </span>
                </a>
              </div>
            </div>
          </div>
          <!-- / dropdown -->
        </li>
        <li class="dropdown" v-toggleClass="{class:'open',close:true}">
          <a class="dropdown-toggle clear">
            <span class="thumb-sm avatar pull-right m-t-n-sm m-b-n-sm m-l-sm">
              <img src="../../assets/app/img/a0.jpg" alt="...">
              <i class="on md b-white bottom"></i>
            </span>
            <span class="hidden-sm hidden-md">John.Smith</span> <b class="caret"></b>
          </a>
          <!-- dropdown -->
          <ul class="dropdown-menu animated fadeInRight w">
            <li class="wrapper b-b m-b-sm bg-light m-t-n-xs">
              <div>
                <p>300mb of 500mb used</p>
              </div>
            </li>
            <li>
              <a href>
                <span class="badge bg-danger pull-right">30%</span>
                <span>Settings</span>
              </a>
            </li>
            <li>
              <a>Profile</a>
            </li>
            <li>
              <a>
                <span class="label bg-info pull-right">new</span>
                Help
              </a>
            </li>
            <li class="divider"></li>
            <li>
              <a>Logout</a>
            </li>
          </ul>
          <!-- / dropdown -->
        </li>
      </ul>
      <!-- / navbar right -->
    </div>
    <!-- / navbar collapse -->
  </div>
</template>
<script>
import fullscreen from './Header-fullscreen'
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'headerNav',
  data() {
    return {
      msg: 'Nav',
    }
  },
  props:['postTitle'],
  computed: {
    ...mapGetters([ 'sidebar','settings'])
  },
  methods: {
    ...mapActions([
      'toggleSidebar'
    ]),
  },
  components:{
    'v-fullscreen':fullscreen
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>


</style>



