<template>
  <div>
    <v-topTab selectPageTitle=""></v-topTab>
    <div class="wrapper-md" style="background-color: #ccccccbf;">
      <div class="panel panel-default">
        <div class="row wrapper">
          <label class="fl" style="line-height:3rem"></label>
          <div class="fl m-b-xs" style="line-height:2.8rem">
            <input type="date" v-model="queryData.params.startTime" /> -
            <input type="date" v-model="queryData.params.endTime" />
          </div>
            <button type="buttton" @click="searchList" class="btn btn_style fl"></button>
            <button type="buttton" @click="resetSearch" class="btn btn_style fl"></button>
          
			<a href="http://r3x0867731.imdo.co/runninglog/export" class="btn btn_style fr"></a>
        </div>
        <div class="table-responsive">
        <div class="width" style="height:2rem;line-height:2rem;"></div>
          <table class="table table-striped b-t b-light">
            <thead>
              <tr>
                
                <th>#</th>
                <th></th>
                <th></th>
                <th></th>
                <th>1#</th>
                <th>2#</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody  v-if="runingList.length>0">
              <tr v-for="(item,index) in runingList" :key="index">
                <td>{{index+1}}</td>
                <td>{{item.hour}}</td>
                <td>{{item.value1}}</td>
                <td>{{item.value2}}</td>
                <td>{{item.value3}}</td>
                <td>{{item.value4}}</td>
                <td>{{item.value5}}</td>
                <td>{{item.value6}}</td>
                <td>{{item.value7}}</td>
                <td>{{item.value8}}</td>
                <td>{{item.value9}}</td>
                <td>{{item.value10}}</td>
                <td>{{item.value11}}</td>
                <td>{{item.value12}}</td>
                <td :style="item.tag1==0?'color:red':'color:green;'">{{isChecked(item.tag1)}}</td>
                <td :style="item.tag2==0?'color:red':'color:green;'">{{isChecked(item.tag2)}}</td>
                <td :style="item.tag3==0?'color:red':'color:green;'">{{isChecked(item.tag3)}}</td>
                <td :style="item.tag4==0?'color:red':'color:green;'">{{isChecked(item.tag4)}}</td>
                <td :style="item.tag5==0?'color:red':'color:green;'">{{isChecked(item.tag5)}}</td>
                <td :style="item.tag6==0?'color:red':'color:green;'">{{isChecked(item.tag6)}}</td>
                <td :style="item.tag7==0?'color:red':'color:green;'">{{isChecked(item.tag7)}}</td>
                <td :style="item.tag8==0?'color:red':'color:green;'">{{isChecked(item.tag8)}}</td>
                <td :style="item.tag9==0?'color:red':'color:green;'">{{isChecked(item.tag9)}}</td>
                <td :style="item.tag10==0?'color:red':'color:green;'">{{isChecked(item.tag10)}}</td>
                <td :style="item.tag11==0?'color:red':'color:green;'">{{isChecked(item.tag11)}}</td>
                <td :style="item.tag12==0?'color:red':'color:green;'">{{isChecked(item.tag12)}}</td>
                <td :style="item.tag13==0?'color:red':'color:green;'">{{isChecked(item.tag13)}}</td>
                <td :style="item.tag14==0?'color:red':'color:green;'">{{isChecked(item.tag14)}}</td>
                <td>{{item.operatorDuty}}</td>
              </tr>
            </tbody>
          </table>
            <div v-if="runingList.length==0" class="cont"></div>
        </div>
      </div>
    </div>
        <footer class="panel-footer width" v-if="runingList.length>0" style="
    right: 0;
    bottom: 0;">
          <div class="row">
            <div class="text-right text-center-xs fr" style="width: 80%;
    padding: 0px 3%;">
              {{(listData.page-1)*20+1}}-{{listData.page*20}}  {{listData.totalNum}}
              <span class="marginLeft1 opt" style="color:#2793e0" @click="page_click($event,1)"></span>
              <span class="marginLeft1 opt" :style="listData.page==1?'color:#ccc':'color:#2793e0'" @click="page_click($event,listData.page-1,listData.page==1)"></span>
              <span class="marginLeft1">{{listData.page}}</span>
              <span class="marginLeft1">{{listData.totalPage}}</span>
              <span class="marginLeft1 opt" :style="listData.page == listData.totalPage?'color:#ccc':'color:#2793e0'" @click="page_click($event,listData.page+1,listData.page == listData.totalPage)"></span>
              <span class="marginLeft1 opt" style="color:#2793e0" @click="page_click($event,listData.totalPage)"></span>
              <span class="marginLeft1"></span> <input type="number" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'  style="width:5rem;margin: 0 1%;" @blur="page_click($event)" :value="listData.page" min="1" :max="listData.totalPage"> 
            </div>
          </div>
        </footer>
  </div>
</template>
<script>
import topTab from "../block/TopTab";
export default {
  name: "runingLog",
  data() {
    return {
      listData: {
          page:1,
          totalNum:0,
          totalPage:1
      },
      runingList:[],
      checkAll: false,
      exportData:{
          startTime:"",
          endTime:"",
          idArrs:[]
      },
      obj: {},
      queryData: {
        params: {
          endTime:"2020-04-14",
          operatorDuty:"",
          page: 1,
          rows: 20,
          startTime: "2020-04-18"
        }
      }
    };
  },
  mounted(){
    this.queryData.params.startTime = this.getDate(10);
    this.queryData.params.endTime = this.getDate(-10);
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.$ajax
        .get("/runninglog/list", this.queryData, {
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
            this.listData = response.data.data;
            this.runingList = response.data.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },
    getDate(n){
       var n = n;
        var d = new Date();
        var year = d.getFullYear();
        var mon = d.getMonth() + 1;
        var day = d.getDate();
        if(day <= n) {
            if(mon > 1) {
                mon = mon - 1;
            } else {
                year = year - 1;
                mon = 12;
            }
        }
        d.setDate(d.getDate() - n);
        year = d.getFullYear();
        mon = d.getMonth() + 1;
        day = d.getDate();
        let s = year + "-" + (mon < 10 ? ('0' + mon) : mon) + "-" + (day < 10 ? ('0' + day) : day);
        return s;
    },
    isChecked(value){
      if(value==0){
        return 'x'
      }else{
        return ''
      }
    },
    page_click(event,page,flag){
        let type = event.type;
        if(type == 'blur'){
            this.queryData.params.page = event.target.value;
            this.init();
        }else{
            if(flag==undefined){
                this.queryData.params.page = page;
                this.init();
            }else{
                if(!flag){
                    this.queryData.params.page = page;
                    this.init();
                }
            }
        }

    },
    searchList() {
        this.init();
    },
    resetSearch() {
        this.queryData.params.startTime = this.getDate(10);
        this.queryData.params.endTime = this.getDate(-10);
        this.init();
      },
    exportOpt() {
        console.info("=======exportOpt=======");
       this.$ajax
        .get("/runninglog/export",this.exportData,{
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          if(response.data.code==200){
            alert('')
            this.init();
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    setCheckAll(event){
        let checked = event.target.checked;
        let arrs = document.getElementsByClassName('form-check-input');
        if(checked){
            for(let i=0;i<arrs.length;i++){
                arrs[i].checked = true;
            }
            for(let a=0;a<this.runingList.length;a++){
                this.exportData.idArrs.push(this.runingList[a].id);
            }
        }else{
            for(let i=0;i<arrs.length;i++){
                arrs[i].checked = false;
            }
            this.exportData.idArrs = [];
        }
        console.info("exportData.idArrs"+this.exportData.idArrs)
    },
    setCheckOpt(event,id){
        let checked = event.target.checked;
        let idArrs = this.exportData.idArrs;
        if(checked){
            let flag = false;
            for(let a=0;a<idArrs.length;a++){
                if(idArrs[a] == id){
                    flag = true;
                }
            }
            if(!flag){
                this.exportData.idArrs.push(id);
            }
        }else{
            for(let a=0;a<idArrs.length;a++){
                if(idArrs[a] == id){
                    this.exportData.idArrs.splice(a,1);
                }
            }
        }
        console.info("exportData.idArrs"+this.exportData.idArrs)
    }
  },
  components: {
    "v-topTab": topTab
  },
};
</script>
<style scoped>
html,
body {
  margin: 0;
  height: 100%;
}
#test {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0.3;
  /*IE8*/
  filter: alpha(opacity=30);
  display: none;
}

#log_window {
  width: 60%;
  margin: 0 20%;
  height: 39rem;
  background-color: #cccccc;
  position: absolute;
  margin-top: 5rem;
  z-index: 3;
  top: 3%;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
}
</style>


