<template>
  <div class="inspectionManager">
    <v-topTab selectPageTitle=""></v-topTab>
    <div class="wrapper-md" style="background-color: #ccccccbf;">
      <div class="panel panel-default">
        <div class="row wrapper">
          <label class="fl" style="line-height:3rem"></label>
          <div class="fl m-b-xs" style="line-height:2.8rem">
            <input type="date" v-model="queryData.params.startTime" /> -
            <input type="date" v-model="queryData.params.endTime" />
          </div>
            <button type="buttton" @click="searchList" class="btn btn_style fl"></button>
            <button type="buttton" @click="resetSearch" class="btn btn_style fl"></button>
			<a href="http://r3x0867731.imdo.co/inspectionRecord/export" class="btn btn_style fr"></a>
            <button type="buttton" @click="addOpt" class="btn btn_style fr"></button>
        </div>
        <div class="table-responsive">
          <table class="table table-striped b-t b-light">
            <thead>
              <tr>
                <th>#</th>
                <th></th>
                <th></th>
                <th></th>
                <th>()</th>
                <th>()</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody  v-if="inspectionRecordList.length>0">
              <tr v-for="(item,index) in inspectionRecordList" :key="index">
                <td>{{index+1}}</td>
                <td>{{item.serial}}</td>
                <td>{{item.templateId}}</td>
                <td>{{item.conclusion}}</td>
                <td>{{item.staff1}}</td>
                <td>{{item.staff2}}</td>
                <td>{{item.duty}}</td>
                <td>{{item.checkPerson}}</td>
                <td>{{item.description}}</td>
                <td>{{timeFormat(item.createTime)-timeFormat(item.updateTime)}}</td>
                <td>
                  <a class="fl width50 opt" @click="updateByUserId(index)"></a>
                  <a class="fl width50 opt" @click="deleteByIdInfo(item.id)"></a>
                </td>
              </tr>
            </tbody>
          </table>
            <div v-if="inspectionRecordList.length==0" class="cont"></div>
        </div>
      </div>
    </div>
        <footer class="panel-footer width" v-if="inspectionRecordList.length>0" style="right: 0;bottom: 0;">
          <div class="row">
            <div class="text-right text-center-xs fr" style="width: 80%;
    padding: 0px 3%;">
              {{(listData.page-1)*20+1}}-{{listData.page*20}}  {{listData.totalNum}}
              <span class="marginLeft1 opt" style="color:#2793e0" @click="page_click($event,1)"></span>
              <span class="marginLeft1 opt" :style="listData.page==1?'color:#ccc':'color:#2793e0'" @click="page_click($event,listData.page-1,listData.page==1)"></span>
              <span class="marginLeft1">{{listData.page}}</span>
              <span class="marginLeft1">{{listData.totalPage}}</span>
              <span class="marginLeft1 opt" :style="listData.page == listData.totalPage?'color:#ccc':'color:#2793e0'" @click="page_click($event,listData.page+1,listData.page == listData.totalPage)"></span>
              <span class="marginLeft1 opt" style="color:#2793e0" @click="page_click($event,listData.totalPage)"></span>
              <span class="marginLeft1"></span> <input type="number" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'  style="width:5rem;margin: 0 1%;" @blur="page_click($event)" :value="listData.page" min="1" :max="listData.totalPage"> 
            </div>
          </div>
        </footer>
    <div id="test"></div>
    <div id="log_window">
         <div class="titleInfo width backgroundFFF">
             <h3 class=" lineHeight5 cont" style="margin:0">
                 <span v-if="Objdata.attendance==null||Objdata.attendance.id==undefined"></span>
                 <span v-if="Objdata.attendance!=null&&Objdata.attendance.id!=undefined"></span>
                 </h3>
             <span class="fr closeX" @click="closeDlg">x</span>
        </div>
       <div class="titleInfo width marginTop1" style="height: 100%;
    background-color: #fff;">
            <div class="item_info fl">
               <label class="">(*):</label>
               <input class="width" type="text" v-model="Objdata.attendance.serial"/>
           </div>
           <div class="item_info fl">
               <label class="">(*):</label>
               <input class="width" type="number" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )' v-model="Objdata.attendance.templateId"/>
           </div>
           <div class="item_info fl">
               <label class="">(*):</label>
               <input class="width" type="number" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'  v-model="Objdata.attendance.conclusion"/>
           </div>
           <div class="item_info fl">
               <label class="">()(*):</label>
               <input class="width" type="text" v-model="Objdata.attendance.staff1"/>
           </div>
           <div class="item_info fl">
               <label class="">()(*):</label>
               <input class="width" type="text" v-model="Objdata.attendance.staff2"/>
           </div>
           <div class="item_info fl">
               <label class="">(*):</label>
               <input class="width" type="text" v-model="Objdata.attendance.checkPerson"/>
           </div>
           <div class="item_info fl">
               <label class="">(*):</label>
               <input class="width" type="number" onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'  v-model="Objdata.attendance.duty"/>
           </div>
           <div class="item_info fl">
               <label class=""></label>
               <input class="width" type="text" v-model="Objdata.attendance.problem1"/>
           </div>
           <div class="item_info fl">
               <label class=""></label>
               <input class="width" type="text" v-model="Objdata.attendance.problem2"/>
           </div>
           <div class="item_info fl">
               <label class=""></label>
               <input class="width" type="text" v-model="Objdata.attendance.problem3"/>
           </div>
           <div class="item_info fl">
               <label class=""></label>
               <input class="width" type="text" v-model="Objdata.attendance.problem4"/>
           </div>
           <div class="item_info fl">
               <label class=""></label>
               <input class="width" type="text" v-model="Objdata.attendance.problem5"/>
           </div>
           <div class="item_info fl">
               <label class=""></label>
               <input class="width" type="text" v-model="Objdata.attendance.problem6"/>
           </div>
           <div class="width fl" style="color:red;padding:0 2%;">*</div>
        <div class="width backgroundFFF fl" style="padding: 2%;height:5rem">
            <button class="btn_style fr" style="margin-right: 3%;" @click="updateInfoPost">
                <span v-if="Objdata.attendance==null||Objdata.attendance.id==undefined"></span>
                <span v-if="Objdata.attendance!=null&&Objdata.attendance.id!=undefined"></span>
                </button>
            <button class="btn_style fr" style="margin-right: 3%;" @click="closeDlg"></button>
        </div>
        </div>
    </div>
  </div>
</template>
<script>
import topTab from "../block/TopTab";
import { attendList, attendAdd } from "../../assets/api/axiosHttp";
export default {
  name: "inspectionManager",
  data() {
    return {
      listData: {
          page:1,
          totalNum:0,
          totalPage:1
      },
      inspectionRecordList:[],
      checkAll: false,
      exportData:{
          startTime:"",
          endTime:"",
          idArrs:[]
      },
      Objdata: {
        attendance:{

        }
      },
      queryData: {
        params: {
          endTime:"2020-04-14",
          page: 1,
          rows: 20,
          startTime: "2020-04-18"
        }
      }
    };
  },
  mounted(){
    this.queryData.params.startTime = this.getDate(10);
    this.queryData.params.endTime = this.getDate(-10);
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.$ajax
        .get("/inspectionRecord/list", this.queryData, {
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
            this.listData = response.data.data;
            this.inspectionRecordList = response.data.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },
    getDate(n){
       var n = n;
        var d = new Date();
        var year = d.getFullYear();
        var mon = d.getMonth() + 1;
        var day = d.getDate();
        if(day <= n) {
            if(mon > 1) {
                mon = mon - 1;
            } else {
                year = year - 1;
                mon = 12;
            }
        }
        d.setDate(d.getDate() - n);
        year = d.getFullYear();
        mon = d.getMonth() + 1;
        day = d.getDate();
        let s = year + "-" + (mon < 10 ? ('0' + mon) : mon) + "-" + (day < 10 ? ('0' + day) : day);
        return s;
    },
    page_click(event,page,flag){
        let type = event.type;
        if(type == 'blur'){
            this.queryData.params.page = event.target.value;
            this.init();
        }else{
            if(flag==undefined){
                this.queryData.params.page = page;
                this.init();
            }else{
                if(!flag){
                    this.queryData.params.page = page;
                    this.init();
                }
            }
        }

    },
    searchList() {
        this.init();
    },
    resetSearch() {
        this.queryData.params.startTime = this.getDate(10);
        this.queryData.params.endTime = this.getDate(-10);
        this.init();
      },
    addOpt(){
        console.info("=======addInfo=======");
       var s = document.getElementById("test");
        s.style.display = "block";
    
        var l = document.getElementById("log_window");
        l.style.display = "block";
    },
    exportOpt() {
        console.info("=======exportOpt=======");
       this.$ajax
        .get("/inspectionRecord/export",this.exportData,{
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          if(response.data.code==200){
            alert('')
            this.init();
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    deleteByIdInfo(id) {
      console.info("=======deleteByIdInfo=======" + id);
       this.$ajax
        .delete("/inspectionRecord/delete/"+id,{
          headers: { "Access-Control-Allow-Origin": "*" }
        })
        .then(response => {
          if(response.data.code==200){
            alert('')
            this.init();
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    closeDlg(){
        var s = document.getElementById("test");
        s.style.display = "none";
    
        var l = document.getElementById("log_window");
        l.style.display = "none";
    },
    updateByUserId(index) {
      this.Objdata.attendance = this.inspectionRecordList[index];
      console.info("=======updateByUserId=======" + index);
       var s = document.getElementById("test");
        s.style.display = "block";
    
        var l = document.getElementById("log_window");
        l.style.display = "block";

    },
    updateInfoPost(){
         if(this.formDataFormat(this.Objdata.attendance)){
         if(this.Objdata.attendance.id == undefined){
            this.$ajax.post("/inspectionRecord/add", this.Objdata, {
            headers: { "Access-Control-Allow-Origin": "*" }
            }).then(response => {
                if(response.data.code==200){
                    alert('')
                this.closeDlg();
                this.init();
            }
            }).catch(response => {
            console.log(response);
            });
         }else{
            this.$ajax.post("/inspectionRecord/modify", this.Objdata, {
            headers: { "Access-Control-Allow-Origin": "*" }
            }).then(response => {
                if(response.data.code==200){
                    alert('')
                this.closeDlg();
                this.init();
            }
            }).catch(response => {
            console.log(response);
            });
         }
        
        var s = document.getElementById("test");
        s.style.display = "none";
        var l = document.getElementById("log_window");
        l.style.display = "none";
         }
    },
    formDataFormat(inspecteData){
      //{serialtemplateIdconclusionstaff1staff2checkPersonduty}
      if(inspecteData.serial!='' && inspecteData.serial!=undefined && 
         inspecteData.templateId!='' && inspecteData.templateId!=undefined && 
         inspecteData.conclusion!='' && inspecteData.conclusion!=undefined && 
         inspecteData.staff1!='' && inspecteData.staff1!=undefined && 
         inspecteData.staff2!='' && inspecteData.staff2!=undefined && 
         inspecteData.checkPerson!='' && inspecteData.checkPerson!=undefined && 
         inspecteData.duty!='' &&  inspecteData.duty!=undefined
        ){
          return true;
        }else{
          return false;
        }
    },
    timeFormat(value) {
      let date = new Date(value);
      let Y = date.getFullYear() + "-";

      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";

      let D = date.getDate() + " ";

      let h = date.getHours() + ":";

      let m = date.getMinutes() + ":";

      let s = date.getSeconds();

      return Y + M + D + h + m + s;
    },
    setCheckAll(event){
        let checked = event.target.checked;
        let arrs = document.getElementsByClassName('form-check-input');
        if(checked){
            for(let i=0;i<arrs.length;i++){
                arrs[i].checked = true;
            }
            for(let a=0;a<this.inspectionRecordList.length;a++){
                this.exportData.idArrs.push(this.inspectionRecordList[a].id);
            }
        }else{
            for(let i=0;i<arrs.length;i++){
                arrs[i].checked = false;
            }
            this.exportData.idArrs = [];
        }
        console.info("exportData.idArrs"+this.exportData.idArrs)
    },
    setCheckOpt(event,id){
        let checked = event.target.checked;
        let idArrs = this.exportData.idArrs;
        if(checked){
            let flag = false;
            for(let a=0;a<idArrs.length;a++){
                if(idArrs[a] == id){
                    flag = true;
                }
            }
            if(!flag){
                this.exportData.idArrs.push(id);
            }
        }else{
            for(let a=0;a<idArrs.length;a++){
                if(idArrs[a] == id){
                    this.exportData.idArrs.splice(a,1);
                }
            }
        }
        console.info("exportData.idArrs"+this.exportData.idArrs)
    }
  },
  components: {
    "v-topTab": topTab
  },
};
</script>
<style scoped>
html,
body {
  margin: 0;
  height: 100%;
}
.inspectionManager .item_info{
    margin: 1% 2%;
    width: 29%;
}
.inspectionManager .lable_Style{
    width: 50%;
}
.inspectionManager #test {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0.3;
  /*IE8*/
  filter: alpha(opacity=30);
  display: none;
}

.inspectionManager #log_window {
  width: 60%;
  margin: 0 20%;
  height: 43%;
  background-color: #cccccc;
  position: absolute;
  margin-top: 5rem;
  z-index: 3;
  top: 15%;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
}
</style>


