'use strict';
import * as types from '../mutation-types'

const state = {
  sidebar: true,
  settings: {
    themeID: 1,
    navbarHeaderColor: 'bg-info dker',
    navbarCollapseColor: 'bg-info dker',
    asideColor: 'bg-white-only',
    headerFixed: true,
    asideFixed: false,
    asideFolded: false,
    asideDock: false,
    container: false
  }
}

const mutations = {
  [types.TOGGLE_SIDEBAR](state, opened) {
    if (opened) {
      state.sidebar = opened
    } else {
      state.sidebar = !state.sidebar
    }
  },
  [types.SETTINGS](state, data) {
      state.settings = data
  } 
}

export default {
  state,
  mutations
}



