<template>
  <div class="app-aside hidden-xs" :class="settings.asideColor">
    <div class="aside-wrap">
      <div class="navi-wrap">
        <!-- list -->
        <nav class="navi">
          <ul class="nav">
            <nav-item class="homePage" :nav-data="{groupName:'',groupIcon:'glyphicon glyphicon-home'}">
            </nav-item>
            <nav-item :nav-data="{groupName:'',groupIcon:'glyphicon glyphicon-list',groupActive:curRoute=='table'}">
              <router-link to="/table/attendanceInfo" tag="li" active-class="active"><a></a></router-link>
              <router-link to="/table/inspectionManager" tag="li" active-class="active"><a></a></router-link>
              <router-link to="/table/runingLog" tag="li" active-class="active"><a></a></router-link>
			  <router-link to="/table/checkPerson" tag="li" active-class="active"><a></a></router-link>
              <!-- <router-link to="/table/footable" tag="li" active-class="active"><a>Foottable</a></router-link> -->
            </nav-item>
          </ul>
        </nav>
        <!-- / list -->
      </div>
    </div>
  </div>
</template>
<script>
import navItem from './Nav-item.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'navBar',
  data() {
    return {
      curRoute: 'table'
    }
  },
  components: {
    'nav-item': navItem
  },
  created() {
    this.setCurNav();
  },
  computed: {
    ...mapGetters(['settings'])
  },
  watch: { // 
    '$route': 'setCurNav'
  },
  methods: {
    setCurNav() {
      //name
      this.curRoute = this.$route.matched[0].name;
      // console.log(this.curRoute)
      // console.log(this.$route)
    }
  }
}

</script>
<!-- Add "scoped " attribute to limit CSS to this component only -->
<style>
.homePage .text-muted{
  display: none;
}

</style>



